import { MODULE_ID } from '@utils/module'
import { getSettingLocalizationPath } from '@utils/foundry/path'

export function getSetting<T extends any>(key: string) {
    return game.settings.get(MODULE_ID, key) as T
}

export function setSetting(key: string, value: unknown) {
    return game.settings.set(MODULE_ID, key, value)
}

/**
 * options.scope = 'world'
 *
 * options.config = false
 */
export function registerSetting<
    TType extends number | string | boolean | object | Function,
    TChoices extends Record<string, unknown> | undefined
>(options: Omit<SettingRegistration<TType, TChoices>, 'choices'> & { choices?: string[] | Record<string, unknown> }) {
    const name = options.name
    options.scope = options.scope ?? 'world'
    options.config = options.config ?? false
    if (options.config) {
        options.name = getSettingLocalizationPath(name, 'name')
        options.hint = getSettingLocalizationPath(name, 'hint')
    }
    if (Array.isArray(options.choices)) {
        options.choices = options.choices.reduce((choices, choice) => {
            choices[choice] = getSettingLocalizationPath(name, 'choices', choice)
            return choices
        }, {} as Record<string, unknown>)
    }
    game.settings.register<TType, TChoices>(MODULE_ID, name, options as SettingRegistration<TType, TChoices>)
}

/**
 * options.restricted = true
 *
 * options.icon = 'fas fa-cogs'
 */
export function registerSettingMenu(
    options: RequiredBy<Partial<Omit<SettingSubmenuConfig, 'type'>>, 'name'> & { type: ConstructorOf<FormApplication> }
) {
    const name = options.name
    options.name = getSettingLocalizationPath('menus', name, 'name')
    options.label = getSettingLocalizationPath('menus', name, 'label')
    options.hint = getSettingLocalizationPath('menus', name, 'hint')
    options.restricted = options.restricted ?? true
    options.icon = options.icon ?? 'fas fa-cogs'
    game.settings.registerMenu(MODULE_ID, name, options as SettingSubmenuConfig)
}

export function getCombatTrackerConfig() {
    return game.settings.get('core', 'combatTrackerConfig') as { resource: string; skipDefeated: boolean }
}
